/* PhotographerPost.css */

.photographer-post {
    max-width: 800px;
    margin: 200px auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
  }
  
  .photographer-post h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .photographer-post img {
    max-width: 100%;
    height: auto;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .photographer-post p {
    font-size: 16px;
    margin-bottom: 8px;
  }
  
  /* Add more styles for other elements as needed */
  